.menuitemdetail {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.menuitemdetail-p {
  margin: 3rem 0;
}

.menuitemdetail-img {
  width: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5rem;
}

.menuitemdetail-img img {
  width: 80%;
}

@media screen and (max-width: 1150px) {
  .menuitemdetail-img {
    margin-bottom: 3rem;
  }
  .menuitemdetail-img img {
    width: 100%;
  }
}
